<template>
  <div class="itemBox">
    <p class="title">
      线下门店&nbsp;
      <span>建议一个页面仅添加一个门店组件</span>
    </p>
    <el-form ref="form" :model="itemData" size="mini">
      <el-form-item label="门店地址" class="navigation">
        <div class="goodsUploadListBtn">
          <ol class="goodsUploadList" v-show="itemData.shopsData.length!=0">
            <li
              v-for="(item,index) in itemData.shopsData"
              :key="index"
              @mouseover="mouseOver(index)"
              @mouseleave="mouseLeave"
            >
              <img :src="item.roomPicture" alt />
              <img
                class="delBtn"
                :class="hoverIndex==index?'delBtnShow':''"
                src="@/assets/decorateBox/goods/close.png"
                @click="delGoods(index)"
                alt
              />
            </li>
          </ol>
          <div class="goodsUpload" @click="addShops" v-show="itemData.shopsData.length<3">+</div>
        </div>

        <p class="prompt">
          最多添加3家门店，在首页只展示添加
          的第一家门店。若您在下方样式处选择了【详实样式】，则
          在首页的门店图片也只展示您第一个门店的第一张图片，所有门店
          会在二级界面按您添加门店地址的顺序，从上到下展示
        </p>
      </el-form-item>
      <el-form-item label="列表样式" class="navigation">
        <el-radio-group v-model="itemData.switchShop">
          <el-radio :label="0">详实样式</el-radio>
          <el-radio :label="1">极简样式</el-radio>
        </el-radio-group>
        <ul>
          <li>
            <p>详实样式：</p>
            <p>优势- 展示内容丰富</p>
            <p>劣势- 若添加的第一个门店的第一张图片不好看，会</p>
            <p>影响店铺首页的整体美观度，且该样式占据首屏的位</p>
            <p>置过大，不利于您店铺首页后续内容的展示</p>
          </li>
          <li>
            <p>极简样式：</p>
            <p>优势-只展示店铺主要信息，占据首页位置不大，您</p>
            <p>放在哪个位置也不会影响整体美观度，也有利于您店</p>
            <p>铺首页后续内容更多的展示在首屏</p>
            <p>劣势-展示店铺信息较少</p>
          </li>
        </ul>
      </el-form-item>
      <el-form-item class="expore">
        <el-button type="primary" plain>门店地址二级界面示例</el-button>
      </el-form-item>
    </el-form>
    <!-- {{itemData}} -->
    <!-- 店铺盒子 -->

    <el-dialog
      title="获取门店地址"
      :visible.sync="dialogVisible"
      width="76%"
      :close-on-click-modal="false"
      @closed="closed"
      @close="submitFormqx"
      :append-to-body="true"
      class="add-dialog" top="6vh" >
      <el-form class="search" ref="search1" :model="search1" label-width="76px" size="mini">
        <el-form-item label="门店名称:" prop="name">
          <el-input v-model="search1.name" placeholder="请输入门店名称"></el-input>
        </el-form-item>
        <div class="formBtnBox">
          <el-button @click="resetForm('search1')" size="mini">重置</el-button>
          <el-button type="primary" icon="el-icon-search" @click="searchSubmit" size="mini">查询</el-button>
        </div>
      </el-form>
      <el-table
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        stripe
        height="380"
        v-loading="loading"
        @selection-change="handleSelectionChange"
        :header-cell-style="{background:'#f5f7fa',color:'#606266'}"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="编号" width="70" align="center" type="index">
          <template slot-scope="scope">{{scope.$index+(nowPage - 1) * pageNumber + 1}}</template>
        </el-table-column>

        <el-table-column label="门店图片" align="center">
          <template slot-scope="scope">
            <el-popover placement="right" title trigger="hover">
              <el-image slot="reference" :src="scope.row.roomPicture" :alt="scope.row.roomPicture" style="max-height: 40px;max-width: 40px"></el-image>
              <el-image :src="scope.row.roomPicture" style="max-height: 350px;max-width: 350px"></el-image>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="门店名称" show-overflow-tooltip align="center">
          <template slot-scope="scope">{{ scope.row.roomName}}</template>
        </el-table-column>
        <el-table-column label="门店地址" show-overflow-tooltip align="center">
          <template slot-scope="scope">{{ scope.row.provinceName}}{{ scope.row.cityName}}{{ scope.row.areaName}}{{ scope.row.locationDesc}}</template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="nowPage"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="pageNumber"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm()" :loading="loadingbut">{{loadingbuttext}}</el-button>
        <el-button @click="submitFormqx()">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { selectShopRoomList } from "@/api/shop/shopDecoration";
import { mapState } from 'vuex'
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object,
      default:{}
    },
  },
  watch:{
    itemData(newvalue,oldValue){
      if(newvalue){
        console.log('执行了')
      }
    },
    immediate:true,
    deep:true
  },
  created(){
    console.log('editShopBox的itemData',this.itemData)
    this.$set(this.itemData)
    console.log(this.getData)
  },
  data() {
    return {
      hoverIndex: -1, //hover的index
      loadingbut: false, //审核提交加载中
      loadingbuttext: "确 定",
      pageNumber: 5, //默认分页条数
      nowPage: 1, //默认第几页
      total: 1,
      tableData: [],
      search: {
        name: ""
      },
      search1: {
        name: ""
      },
      multipleSelection: [], //选中的数据
      loading: false,
      dialogVisible: false
    };
  },
  methods: {
    //弹窗取消按钮
    submitFormqx() {
      this.dialogVisible = false;
      this.$refs.search1.resetFields();
      this.search.name = "";
    },

    // 选中的
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //弹窗确定按钮
    submitForm() {
      let pd = Number(this.multipleSelection.length) + Number(this.itemData.shopsData.length);
      if (this.multipleSelection.length != 0 && pd <= 3) {
        this.loadingbut = true;
        this.loadingbuttext = "提交中...";
        this.dialogVisible = false;
        this.multipleSelection.map(item => {
          this.itemData.shopsData.push(item);
        });
      } else if (pd > 3) {
        this.$message.error("	选择的门店总数不能超过三个");
      } else {
        this.$message.error("请选择商品");
      }
    },
    // 请求商品
    required(pageNo, pageSize, ids) {
      let params = {
        pageNum: pageNo,
        pageSize: pageSize,
        shopId: this.shop.shopId,
        roomName: this.search.name,
        ids: ids //查询没有选过的商品
      };
      this.loading = true;
      selectShopRoomList(params).then(res => {
        if (res.code == 200) {
          this.tableData = res.data.list;
          this.total = res.data.total;
          this.loading = false;
        }
      });
    },
    // 弹窗并选择商品
    addShops() {
      this.dialogVisible = true;
      let id = [];
      this.itemData.shopsData.map(item => {
        id.push(item.id);
      });
      let ids = id.join(",");
      console.log(ids);
      console.log(this.itemData.shopsData);
      this.required(this.nowPage, this.pageNumber, ids);
    },
    // 分页 - 切换页面
    handleCurrentChange(val) {
      this.nowPage = val;
      this.required(val, this.pageNumber);
    },
    // 分页 -
    handleSizeChange(val) {
      this.pageNumber = val;
      this.required(1, val);
    },
    closed() {
      this.loadingbut = false;
      this.loadingbuttext = "确 定";
    },

    //重置
    resetForm(formName) {
      this.$nextTick(() => {
        this.$refs[formName].resetFields();
        this.search.name = "";
        this.searchSubmit()
      });
    },
    // 搜索 - 点击搜索
    searchSubmit() {
      this.search.name = this.search1.name;
      this.required(1, 5);
    },
    // 移入事件
    mouseOver(index) {
      this.hoverIndex = index;
    },
    // 移出事件
    mouseLeave() {
      this.hoverIndex = -1;
    },
    // 删除商品
    delGoods(index) {
      this.itemData.shopsData.splice(index, 1);
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ]),
    getData(){
      console.log('指导性了')
      return this.itemData
    }
  },
};
</script>

<style scoped lang="less">
.active {
  border: 1px solid #000;
}
.title {
  border-bottom: 1px solid #eee;
  padding: 20px 18px 24px 18px;
  color: #333;
  font-size: 16px;
  font-weight: 700;
  span {
    height: 14px;
    font-size: 12px;
    color: rgba(153, 153, 153, 1);
    line-height: 14px;
    letter-spacing: 0px;
    font-weight: 100;
  }
}
.el-dialog__body {
  .pagination {
    padding-top: 20px;
  }
  .search {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    .el-form-item {
      margin-bottom: 0px;
      margin-right: 20px;
    }
    .formBtnBox {
      margin-left: 20px;
    }
  }
  img {
    width: 100px;
    margin: 0 auto;
  }
}
.itemBox /deep/ .el-form {
  margin: 18px 24px;
  .el-form-item__label {
    display: block;
    float: inherit;
    text-align: inherit;
    padding: 0;
    color: #222;
    font-weight: 100;
    margin-bottom: 12px;
  }
  .el-input__inner {
    height: 34px;
    border-radius: 2px;
  }
  .el-form-item__error {
    padding-top: 5px;
  }
  .el-radio__label {
    font-size: 12px;
    color: #333;
  }
  .expore {
    text-align: right;
  }
  .navigation {
    border-bottom: 1px solid #eee;
    padding-bottom: 16px;
    .el-radio {
      margin-left: 10px;
      width: 60px;
      margin-bottom: 10px;
    }
    ul {
      background-color: #fcfcfc;
      padding: 12px 10px;
      margin-top: 3px;
      li {
        p {
          font-size: 12px;
          color: rgba(102, 102, 102, 1);
          line-height: 20px;
          letter-spacing: 0px;
        }
      }
      li:first-child {
        margin-bottom: 10px;
      }
    }
  }
  .custom {
    display: flex;
    align-items: center;
    .el-color-picker {
      margin-left: 16px;
    }
  }
  .el-checkbox-group {
    margin-left: 10px;
  }
  .el-checkbox {
    display: block;
    margin: 0;
  }
  .prompt {
    font-size: 12px;
    color: rgba(153, 153, 153, 1);
    line-height: 20px;
    letter-spacing: 0px;
    margin-top: 5px;
  }
  .goodsUploadListBtn{
    display: flex;
  }
  .goodsUploadList {
    display: flex;
    flex-wrap: wrap;
    li {
      width: 68px;
      height: 68px;
      transition: 0.2s;
      cursor: pointer;
      border: 1px solid #ddd;
      box-sizing: border-box;
      padding: 4px;
      position: relative;
      margin-right: 10px;
      margin-bottom: 10px;
      img {
        width: 100%;
        height: 100%;
      }
      .delBtn {
        width: 16px;
        height: 16px;
        position: absolute;
        right: -8px;
        top: -8px;
        display: none;
      }
      .delBtnShow {
        display: block;
      }
    }
    li:hover {
      border: 1px solid #6babec;
    }
  }
  .goodsUpload {
    font-size: 34px;
    background: #fcfcfc;
    font-weight: 100;
    color: #9a9a9a;
    width: 68px;
    height: 68px;
    border-radius: 4px 4px 4px 4px;
    text-align: center;
    line-height: 68px;
    border: 1px dashed #ddd;
    margin-bottom: 10px;
    transition: 0.2s;
    cursor: pointer;
  }
  .goodsUpload:hover {
    background-color: #ecf5ff;
    border: 1px dashed rgba(0, 121, 253, 1);
  }
}
</style>
